@import "../../../css/module/fn";

body{
	overflow: hidden;
}

#slider{
	height: p2m(420);
	img{
		width: 10rem;
		height: p2m(420);
		background-color: #777777;
	}
}
#banner {
	position: absolute;
	z-index: 2;
	width: 100%;
}

.mui-inner-wrap {
	background-color: #fff;
}

#message {
	height: p2m(96);
	line-height: p2m(96);
	background-color: #eee;
	padding: 0 0.5rem 0 1.6rem;
	font-size: 0.3rem;
	text:{
		//indent: 1.5rem;
		overflow: ellipsis;
	}
	overflow:hidden;
	background: {
		image: url("/img/ico96-lb.png");
		size: 1.28rem ;
		repeat: no-repeat;
		position: left center;
	}
	white-space: nowrap;
	
}

#myScroll{
	position: absolute;
	top: p2m(516);
	bottom: 0;
	overflow: scroll;
}

#board {
	width: 100%;
	table-layout: fixed;
	td {
		height: 3.1rem;
		border: 1px solid #ddd;
		border-collapse: collapse;
		background-color: #fff;
		text: {
			align: center;
			transform: capitalize;
		}
		font: {
			size: 0.35rem;
		}
		
		&:nth-of-type(3n+0) {
			border-right: none;
		}
		&:nth-of-type(3n-2) {
			border-left: none;
		}
	}
	
	.icoDashboard {
		display: block;
		width: 1.28rem;
		height: 1.28rem;
		margin: 0 auto 0.2rem;
	}
	
	
}

.mui-icon-bars:before{
	display: block;
	color: #fff;
	width: 0.64rem;
	height: 0.64rem;
	margin: 0.6rem 0 0 0.4rem;
}

#imgQR{
	color: #fff;
	width: 0.64rem;
	height: 0.64rem;
	margin: 0.6rem 0.4rem 0 0;
}

#offCanvasSide{
	background: {
		image: url("./bg-leftMenu2.jpg");
		size: 100% 100%;
		repeat: no-repeat;
	}
}
#imgAvatar{
	width: 2.547rem;
	height: 2.547rem;
	display: block;
	margin: 1.333rem auto 0.53rem;
}
#spUserName{
	text-transform: uppercase;
	text-align: center;
	font-size: 0.45rem;
	color: #fff;
}
#spRealName{
	text-align: center;
	font-size: 0.35rem;
	color: #bbb;
	margin-bottom: 1.33rem;
}

#divITB{
	display: block;
	position: absolute;
	bottom: 0.4rem;
	left: 0;
	text-align: center;
	width: 100%;
	
	img{
		width: 2.2rem;
		margin: 0 auto 0.2rem;
	}
	
	span{
		display: block;
		font-size: 0.3rem;
	}
}

#offCanvasSideScroll{
	top: 6.66rem;
	.mui-table-view{
		background-color: #deb5a7;
		li{
			color: #89351e;
			
			img{
				width: 16px;
				height: 16px;
				vertical-align: sub;
			}
		}
	}
	.mui-table-view:after,.mui-table-view-cell:after{
		background-color:#89351e;
	}
}